.Button {

    padding: 40px 80px;
    font-size: 2em;
    border-radius: 30px;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 60px;
    cursor: pointer;
}

.Menu {

    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 60vh;
    z-index: 100;
}